// BiDirectional Edge Component
const BiDirectionalEdge = ({
  id,
  sourceX,
  sourceY,
  targetX,
  targetY,
  sourcePosition,
  targetPosition,
  style = {},
  markerEnd,
  markerStart,
  label,
  labelStyle = {},
  labelShowBg = true,
  labelBgStyle = {},
  labelBgPadding = [8, 4],
}) => {
  const getBezierPath = (params) => {
    const { sourceX, sourceY, sourcePosition, targetX, targetY, targetPosition } = params;

    const [_centerX, _centerY, offsetX, offsetY] = [
      (sourceX + targetX) / 2,
      (sourceY + targetY) / 2,
      Math.abs(targetX - sourceX) / 4,
      Math.abs(targetY - sourceY) / 4,
    ];

    let path = '';

    if (sourcePosition === Position.Bottom && targetPosition === Position.Top) {
      path = `M${sourceX},${sourceY} C${sourceX},${sourceY + offsetY} ${targetX},${targetY - offsetY} ${targetX},${targetY}`;
    } else if (sourcePosition === Position.Right && targetPosition === Position.Left) {
      path = `M${sourceX},${sourceY} C${sourceX + offsetX},${sourceY} ${targetX - offsetX},${targetY} ${targetX},${targetY}`;
    } else if (sourcePosition === Position.Left && targetPosition === Position.Right) {
      path = `M${sourceX},${sourceY} C${sourceX - offsetX},${sourceY} ${targetX + offsetX},${targetY} ${targetX},${targetY}`;
    } else {
      path = `M${sourceX},${sourceY} L${targetX},${targetY}`;
    }

    return [path, (sourceX + targetX) / 2, (sourceY + targetY) / 2];
  };

  const [edgePath, labelX, labelY] = getBezierPath({
    sourceX,
    sourceY,
    sourcePosition,
    targetX,
    targetY,
    targetPosition,
  });

  return (
    <g>
      <path
        id={id}
        style={style}
        className="react-flow__edge-path"
        d={edgePath}
        fill="none"
        markerEnd={markerEnd}
        markerStart={markerStart}
      />
      {label && (
        <foreignObject
          width={120}
          height={30}
          x={labelX - 60}
          y={labelY - 15}
          className="edgebutton-foreignobject"
          requiredExtensions="http://www.w3.org/1999/xhtml"
        >
          <div
            style={{
              width: '100%',
              height: '100%',
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
            }}
          >
            <div
              style={{
                backgroundColor: 'white',
                padding: '2px 8px',
                borderRadius: '4px',
                fontSize: '10px',
                fontWeight: 500,
                color: '#374151',
                border: '1px solid #d1d5db',
                boxShadow: '0 1px 2px rgba(0, 0, 0, 0.1)',
                ...labelBgStyle,
                ...labelStyle,
              }}
            >
              {label}
            </div>
          </div>
        </foreignObject>
      )}
    </g>
  );
};

